.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  width: 250px;
  height: 200px;
  position: relative;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.03);
  border-radius: 10px;
  padding: 30px 10px;
}

.card:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.card__img {
  height: 44px;
  width: 44px;
  margin: 0 auto 24px;
}

.card__img img {
  display: block;
  width: 100%;
}

.card__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 6px;
}

.card__text {
  line-height: 18px;
  color: rgba(0, 0, 0, 0.6);
}

.card.card--active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.btn {
  display: inline-block;
  min-width: 165px;
  border-radius: 5px;
  color: #FAFAFA;
  text-align: center;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  padding: 13px 20px;
  transition: all 0.3s ease;
  border: 1px solid #D23166;
}

.btn:hover {
  transition: all 0.3s ease;
}

.btn.primary-btn {
  background-color: #D23166;
  border-color: #D23166;
}

.btn.primary-btn:hover {
  background-color: rgba(210, 49, 102, 0.7);
  color: #FAFAFA;
}

.btn.primary-btn.outline-btn {
  background-color: transparent;
  color: #FAFAFA;
}

.btn.primary-btn.outline-btn:hover {
  background-color: #D23166;
  color: #FAFAFA;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 84px;
}

.item__section {
  padding: 50px 0;
}

.item__img {
  width: 580px;
  height: 415px;
}

.item__img img {
  display: block;
  width: 100%;
}

.item__box {
  max-width: 454px;
}

.item__title {
  font-weight: 700;
  font-size: 26px;
  line-height: 36px;
  margin-bottom: 12px;
}

.item__text {
  font-size: 16px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 28px;
}

.item__group {
  margin-bottom: 16px;
}

.item__sale {
  font-weight: 500;
  font-size: 26px;
  line-height: 36px;
  padding-right: 6px;
}

.item__price {
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  text-decoration-line: line-through;
  color: rgba(0, 0, 0, 0.7);
}

.search {
  position: relative;
}

.search__input {
  width: 100%;
  padding: 23px 0 23px 30px;
  background-color: #FAFAFA;
  line-height: 28px;
  border-radius: 5px;
}

.search__btn {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  width: 134px;
  border-radius: 5px;
  background-color: #D23166;
  color: #ffffff;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
}

.search__btn:hover {
  background-color: rgba(210, 49, 102, 0.8);
}

.subscribe {
  position: relative;
  width: 388px;
}

.subscribe__input {
  width: 100%;
  padding: 18px 0 18px 32px;
  background-color: #000000;
  border: 2px solid #D23166;
  border-radius: 5px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.7);
}

.subscribe__btn {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  width: 125px;
  border-radius: 5px;
  background-color: #D23166;
  color: #ffffff;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
}

.subscribe__btn:hover {
  background-color: rgba(210, 49, 102, 0.8);
}

