.header-section {
  background-color: #F8EDF0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__img {
  width: 80px;
}

.header__img img {
  width: 100%;
}

.header__nav {
  display: flex;
  align-items: center;
  margin: 0 -20px;
}

.header__nav li {
  padding: 0 20px;
}

.header__nav a {
  color: rgba(21, 1, 1, 0.6);
}

.header__nav a:hover {
  color: #D23166;
}

.header__nav a.active {
  color: #D23166;
  font-weight: 700;
}

html * {
  box-sizing: border-box;
}

html *::selection {
  background-color: rgba(210, 49, 102, 0.3);
  color: #000000;
}

body,
html {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  background-color: #ffffff;
}

#wrapper {
  position: relative;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  overflow-y: auto;
  overflow-x: hidden;
}

body {
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0 0 10px;
}

img {
  max-width: 100%;
}

p {
  margin: 0 0 10px;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

input {
  border: 0;
}

input[type="text"], input[type="password"], input[type="email"], input[type="search"] {
  -webkit-appearance: none;
}

button {
  border: 0;
  padding: 0;
  background-color: unset;
  cursor: pointer;
}

textarea {
  resize: vertical;
  -webkit-appearance: none;
}

.container {
  width: 100%;
  height: 100%;
  max-width: 1115px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col-3 {
  display: flex;
  width: 25%;
  padding: 0 15px;
}

.col-4 {
  width: 100%;
  padding: 0 15px;
}

@media (min-width: 1200px) {
  .col-4 {
    width: calc(100% / 3);
  }
}

.col-6 {
  width: 50%;
  padding: 0 15px;
}

.col-9 {
  width: 100%;
  padding: 0 15px;
}

@media (min-width: 1024px) {
  .col-9 {
    width: 66.6%;
  }
}

@media (min-width: 1200px) {
  .col-9 {
    width: 75%;
  }
}

.col-12 {
  padding: 0 15px;
}

@media (min-width: 1200px) {
  .col-12 {
    width: 100%;
  }
}

.d-flex {
  display: flex;
  padding: 0 15px;
  margin-bottom: 10px;
}

.background-primary {
  background-color: #F8EDF0;
}

.background-dark {
  background-color: #000000;
}

.hero-section {
  display: block;
  background: url(../img/ice-cream.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
  min-height: 720px;
  padding-top: 196px;
}

.hero {
  max-width: 665px;
}

.hero__subtitle {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.1em;
  color: #0F0101;
  margin-bottom: 6px;
}

.hero__title {
  font-size: 58px;
  line-height: 69px;
  color: rgba(15, 1, 1, 0.9);
  margin-bottom: 36px;
  font-weight: 600;
}

.hero__title span {
  color: #D23166;
}

.hero__text {
  max-width: 524px;
  font-size: 18px;
  line-height: 27px;
  color: rgba(15, 1, 1, 0.6);
  margin-bottom: 50px;
}

.delivery-section {
  padding: 60px 0;
}

.details-section {
  display: block;
  background: url(../img/ice-cream-2.png);
  background-size: 600px;
  background-repeat: no-repeat;
  background-position: right;
  min-height: 440px;
  padding-top: 80px;
}

.details {
  max-width: 442px;
}

.details__title {
  font-weight: 600;
  font-size: 44px;
  line-height: 50px;
  max-width: 350px;
  margin-bottom: 16px;
}

.details__text {
  font-size: 16px;
  line-height: 24px;
  color: rgba(15, 1, 1, 0.6);
  margin-bottom: 36px;
}

.product-section {
  padding: 100px 0 0;
}

.product__title {
  font-weight: 600;
  font-size: 44px;
  line-height: 62px;
  margin-bottom: 64px;
  text-align: center;
}

.product__nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 -20px;
}

.product__nav li {
  padding: 0 20px;
}

.product__nav a {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.6);
  text-transform: uppercase;
  padding-bottom: 4px;
}

.product__nav a:hover {
  color: #000000;
  border-bottom: 3px solid #D23166;
}

.product__nav a.active {
  color: #000000;
  border-bottom: 3px solid #D23166;
}

.polar-card {
  margin-bottom: 40px;
}

.polar-card__img {
  border-radius: 50%;
  background-color: #D23166;
  overflow: hidden;
  width: 59px;
  height: 58px;
}

.polar-card__box {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 30px;
}

.polar-card__name {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 6px;
}

.polar-card__company {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(0, 0, 0, 0.7);
}

.polar-card__text {
  max-width: 330px;
  font-size: 16px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.7);
  position: relative;
}

.polar-card__text::before {
  display: block;
  content: "\201c";
  color: #F6762C;
  font-size: 60px;
  position: absolute;
  top: 10px;
  left: -28px;
}

.polar-card__text::after {
  display: block;
  content: "\201e";
  color: #F6762C;
  font-size: 60px;
  position: absolute;
  top: 34px;
  right: 8px;
}

.polar-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.polar-nav-card__img {
  height: 35px;
  width: 35px;
  opacity: 0.6;
}

.polar-nav-card__img img {
  display: block;
  width: 100%;
}

.polar-nav-card__img.active {
  height: 58px;
  width: 58px;
  opacity: 1;
}

.arrow {
  border: solid #D23166;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 4px;
}

.arrow.right {
  transform: rotate(-45deg);
}

.arrow.left {
  transform: rotate(135deg);
}

.feedback {
  display: flex;
  justify-content: space-between;
}

.feedback__img {
  width: 564px;
  flex-shrink: 0;
}

.feedback__img img {
  display: block;
  width: 100%;
}

.app-section {
  padding: 90px 0 100px;
  position: relative;
}

.app {
  display: flex;
  align-items: center;
}

.app__img {
  width: 305px;
  height: 628px;
  position: absolute;
  top: 6%;
}

.app__img img {
  display: block;
  width: 100%;
}

.app__box {
  max-width: 630px;
  margin-left: auto;
}

.app__title {
  font-weight: 500;
  font-size: 44px;
  line-height: 52px;
  margin-bottom: 20px;
}

.app__text {
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.01em;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 30px;
}

.app__image {
  height: 40px;
  width: 40px;
}

.app__image img {
  display: block;
  width: 100%;
}

.app__item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.app__item span {
  padding-left: 20px;
  font-size: 22px;
  line-height: 26px;
}

.app__item span:hover {
  color: #D23166;
}

.app__link {
  display: flex;
  align-items: center;
  gap: 30px;
}

.app__btn {
